import { __decorate } from "tslib";
import { Component, Vue } from "vue-property-decorator";
import Home from "./components/Home.vue";
import About from "./components/About.vue";
// import { BootstrapVue } from 'bootstrap-vue'
// import 'bootstrap/dist/css/bootstrap.css'
// import 'bootstrap-vue/dist/bootstrap-vue.css'
let App = class App extends Vue {
    constructor() {
        super(...arguments);
        this.data = null;
        this.dinamicComponent = "Home";
        this.compName = "<mf-3>";
    }
    onClickHome() {
        this.dinamicComponent = "Home";
    }
    onClickAbout() {
        this.dinamicComponent = "About";
    }
    created() {
        console.log("%c<mf-3>", "background: black;padding: 10px;");
        console.log("created() -> data:", this.data);
    }
    async loadApp() {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                console.log("setTimeout()");
                resolve("fetch Data");
            }, 10);
        });
    }
    mounted() {
        console.log("mounted() -> data:", this.data);
        this.loadApp().then((data) => {
            this.data = data;
            console.log("loadApp() -> data:", this.data);
        });
    }
};
App = __decorate([
    Component({
        components: {
            Home,
            About,
        },
    })
], App);
export default App;
